/*-----------------------------------------------------------------------------------------------------
  [elements:page]
-------------------------------------------------------------------------------------------------------*/
body {
  font-family: Arial, sans-serif;
  color: #000000;
  background: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* heading */
h1, h2, h3, h4, h5, h6 {
  font-family: Verdana, Geneva, sans-serif;
  color: #000000;
}

p {
  line-height: 1.3;
}

/* link */
a {
  color: #000000;
}

a:hover {
  color: #000000;
  text-decoration: underline;
}

/* list */
ul,
ol {
  margin: 0;
}

ul li,
ol li {
  margin: 0;
}

/* img */
img {
  display: inline-block;
  max-width: 100%;
  font-style: italic;
}

/* blockquote */
blockquote {
  color: #676767;
  font-style: italic;
  font-family: Georgia, "Times New Roman", serif;
  font-size: 2.8rem;
  position: relative;
  padding: 2rem 0 0 8rem;
}

@media (max-width: 48rem) {
  blockquote {
    font-size: 1.8rem;
    padding: 0 0 0 4rem;
  }
}

blockquote p:before {
  content: " « ";
  color: #000000;
  font-size: 11rem;
  position: absolute;
  left: 0;
  top: 0;
  line-height: 30%;
}

@media (max-width: 48rem) {
  blockquote p:before {
    font-size: 6rem;
  }
}

blockquote p:after {
  content: " » ";
  color: #000000;
  font-size: 6rem;
  line-height: 20%;
  position: relative;
  top: 0.6rem;
}

@media (max-width: 48rem) {
  blockquote p:after {
    font-size: 3rem;
  }
}

blockquote cite {
  font-weight: 700;
  color: #666666;
  display: block;
  font-size: 1.7rem;
  font-family: Arial, sans-serif;
  font-style: normal;
  position: relative;
  padding-left: 5rem;
}

@media (max-width: 48rem) {
  blockquote cite {
    font-size: 1.6rem;
  }
}

blockquote cite span {
  color: #676767;
}

blockquote cite:before {
  content: "";
  display: block;
  width: 4rem;
  height: 2px;
  background-color: #000000;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

/* hr */
hr {
  background: #dddddd;
  border: none;
  height: 1px;
}

/* table */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

table th {
  border: 1px solid gray;
  padding: 0.2em;
  text-align: left;
}

table td {
  border: 1px solid gray;
  padding: 0.2em;
}

